import { Component, ViewChild, Input, SimpleChanges, OnInit } from '@angular/core';
import { DataSource } from '@angular/cdk/table';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import { Quote } from "../model/quote";
import { QuoteService } from "../service/quote.service";
import { Router } from '@angular/router';
import { FavStocksService } from '../service/fav-stocks.service';
import { AngularFireList } from 'angularfire2/database';
import { Favstock } from '../model/favstock';
import { AuthService } from '../service/auth.service';
@Component({
  selector: 'app-quote',
  templateUrl: './quote.component.html',
  styleUrls: ['./quote.component.css']
})

export class QuoteComponent implements OnInit {
  uid: string;
  mapKeySymbol: any;
  quotes = Array<Quote>();
  constructor(private quoteService: QuoteService, private router: Router, private favStockService: FavStocksService, private authService: AuthService) {
    this.mapKeySymbol = new Map();
  }

  @Input() stsymbol: string;
  ngOnChanges(changes: SimpleChanges) {
    let found = false;
    if (this.stsymbol)
      this.quotes.forEach(element => {
        if (element.symbol == this.stsymbol) found = true;
      });
    if (this.stsymbol && !found)
      this.favStockService.addFavStock(this.uid, this.stsymbol);
  }
  ngOnInit() {

    this.refreshval();
    this.authService.user$.subscribe(user => {
      if (user)
        this.uid = user.uid;
      else
        this.uid = 'default';
      this.getFavStocksFromDB();
    });
  }

  refreshval() {
    //console.log("refresh called");
    setTimeout(() => this.refreshval(), 10000);
    this.quotes.forEach(element => {
      this.quoteService.getQuotes(element.symbol).subscribe((p) => {
        this.updateOrPush(p);
      });
    });
  }

  deleteSymbolServer(sym: string) {
    //console.log("delete clicked");
    this.favStockService.remFavStock(this.uid, this.mapKeySymbol[sym]);
  }

  deleteSymbol(sym: string) {
    for (var i = 0; i < this.quotes.length; i++) {
      if (this.quotes[i].symbol == sym) {
        this.quotes.splice(i, 1);
        break;
      }
    }
  }

  addOrUpdate(sym: string) {
    this.quoteService.getQuotes(sym).subscribe((p) => {
      let found = false;
      for (let i = 0; i < this.quotes.length; i++) {
        if (this.quotes[i].symbol.toUpperCase() == sym.toUpperCase()) {
          found = true;
        }
      }
      if (found == false)
        this.quotes.push(p);
      // if (this.quotes.length < 3)
      //   this.router.navigate(['/main', this.quotes[0].symbol.toUpperCase()]);
    });
  }

  updateOrPush(p: Quote) {
    let found = false;
    for (let i = 0; i < this.quotes.length; i++) {
      if (this.quotes[i].symbol == p.symbol) {
        this.quotes[i].latestPrice = p.latestPrice;
        this.quotes[i].changePercent = p.changePercent;
        found = true; break;
      }
    }

    if (found == false)
      this.quotes.push(p);
  }

  getFavStocksFromDB() {
    this.quotes = Array<Quote>();
    var favStocks$ = this.favStockService.getFavStocks(this.uid);
    //console.log('inside getfavstockfrom db' + this.favStocks$);
    favStocks$.subscribe(data => {
      data.forEach(element => {
        this.mapKeySymbol[element.stock] = element.key;
        //console.log(element);
        //console.log(element);
        this.addOrUpdate(element.stock);
      });
      this.quotes.forEach(q => {
        let found = false;
        data.forEach(d => {
          if (q.symbol.toUpperCase() == d.stock.toUpperCase()) {
            found = true;
          }
        });
        if (!found)
          this.deleteSymbol(q.symbol);
      });
    });
  }
}



