import { Component, OnInit } from '@angular/core';

import { PanelModule } from 'primeng/primeng';
import { AngularFireDatabase } from 'angularfire2/database';
import { AuthService } from './service/auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'app';
  selectedStock: string;


  constructor(private db: AngularFireDatabase, private authService: AuthService, private router: Router) {
    //  db.object('connected').(console.log);
  }

  onSelectedStockChange(stock: string) {
    this.selectedStock = stock.toLocaleUpperCase();
  }

  ngOnInit(): void {
    this.authService.user$.subscribe(user => {
      if (user) {
       // console.log('inside port');
        this.router.navigate(['/Portfolio']);
      }
      else {
        this.router.navigate(['/main']);
       // console.log('inside main');
      }
    });
  }

}


