import { Component, OnInit, SimpleChanges, Input } from '@angular/core';
import { Stats } from '../model/stats';
import { StatsService } from '../service/stats.service';
import { QuoteService } from '../service/quote.service';
import { Quote } from '../model/quote';

@Component({
  selector: 'app-key-statistics',
  templateUrl: './key-statistics.component.html',
  styleUrls: ['./key-statistics.component.css']
})
export class KeyStatisticsComponent implements OnInit {
  selectedStock: string;
  stats: Stats;
  quotes: Quote;
  @Input() stsymbol: string = '';
  ngOnChanges(changes: SimpleChanges) {
    this.selectedStock = this.stsymbol;
    this.statsService.getStats(this.stsymbol).subscribe(p => { this.stats = p; });
    this.quoteService.getQuotes(this.stsymbol).subscribe(p => this.quotes = p);
  }
  constructor(private statsService: StatsService, private quoteService: QuoteService) {

  }

  columnNum = 4;
  ngOnInit() {
  }

}



