import { Injectable, OnInit } from '@angular/core';
import { AngularFireDatabase, AngularFireList } from 'angularfire2/database';
import { Favstock } from '../model/favstock';
import { Observable } from 'rxjs/Observable';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { AuthService } from '../service/auth.service';
import { Portfolio } from '../portfolio';

@Injectable()
export class FavStocksService implements OnInit {
  portfolio$: AngularFireList<any>;

  ngOnInit(): void {

  }


  favStocks$: AngularFireList<any>;

  //favStocks$: AngularFireList<{}>;
  // favStocks$: FirebaseListObservable<Favstock[]>;

  intFavStock() {
    this.favStocks$ = this.db.list<any>('favstocks');
    this.portfolio$ = this.db.list<any>('portfolio');

  }

  constructor(private db: AngularFireDatabase, private afs: AngularFirestore, private authService: AuthService) {
    this.intFavStock();

    authService.user$.subscribe(user => {
      if (user)
        console.log('inside constlogin' + user.displayName);
    });

  }

  getFavStocks(uid: string): Observable<Favstock[]> {
    //return this.db.list<any>('favstocks', ref => ref.orderByChild('user').equalTo(uid));

    let favStockRef = this.db.list('favstocks', ref => ref.orderByChild('user').equalTo(uid));
    let favStocks = favStockRef.snapshotChanges().map(changes => {
      return changes.map(c => ({ key: c.payload.key, ...c.payload.val() }));
    });
    return favStocks;
  }

  addFavStock(uid: string, sym: string) {
    this.favStocks$.push({ user: uid, stock: sym });
  }


  getPortfolio(uid: string): Observable<any[]> {
    //return this.db.list<any>('portfolio', ref => ref.orderByChild('user').equalTo(uid));
    let portfolioRef = this.db.list('portfolio', ref => ref.orderByChild('user').equalTo(uid));
    let portfolios = portfolioRef.snapshotChanges().map(changes => {
      return changes.map(c => ({ key: c.payload.key, ...c.payload.val() }));
    });
    return portfolios;
  }

  addPortfolio(uid: string, sym: string, p: number, qty: number) {
    this.portfolio$.push({ user: uid, stock: sym, price: p, quantity: qty });
  }

  updatePortfolio(uid: string, portfolio: Portfolio) {
    let portfolioRef = this.db.list('portfolio', ref => ref.orderByChild('user').equalTo(uid));
    if (portfolio.key.length > 2) {
      portfolioRef.update(portfolio.key.toString(), { user: uid, stock: portfolio.stock, price: portfolio.price, quantity: portfolio.quantity });
    }
    else {
      portfolioRef.push({ user: uid, stock: portfolio.stock, price: portfolio.price, quantity: portfolio.quantity });
    }

  }

  remPortfolio(uid:string, key:string){
    let portfolioRef = this.db.list('portfolio', ref => ref.orderByChild('user').equalTo(uid));
    portfolioRef.remove(key);
  }


  remFavStock(uid: string, sym: string) {
    let favStockRef = this.db.list('favstocks', ref => ref.orderByChild('user').equalTo(uid));
    favStockRef.remove(sym);

  }

  getKeyPortfolio(uid: string, sym: string) {
    var key: any;
    let portfolioRef = this.db.list('portfolio', ref => ref.orderByChild('user').equalTo(uid));
    let portfolios = portfolioRef.snapshotChanges().map(changes => {
      return changes.map(c => ({ key: c.payload.key, ...c.payload.val() }));
    });
    return portfolios;

  }
}



