import { Component, OnInit, ViewChild, ElementRef, Inject } from '@angular/core';
import { DataSource } from '@angular/cdk/collections';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/of';
import { MatSort, MatPaginator, MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import { FavStocksService } from '../service/fav-stocks.service';
import { AuthService } from '../service/auth.service';
import { AngularFireList } from 'angularfire2/database';
import { ActivatedRoute, Router } from '@angular/router';
import { Portfolio } from '../portfolio';
import { QuoteService } from '../service/quote.service';




@Component({
  selector: 'app-portfolio',
  templateUrl: './portfolio.component.html',
  styleUrls: ['./portfolio.component.css']
})
export class PortfolioComponent implements OnInit {

  portfolio$: AngularFireList<any>;
  uid: string;
  PrevClose: number;
  profitPercentage: number;
  canEdit: boolean;

  tot: Portfolio = { stock: "Total", price: 0, quantity: 0, lastPrice: 0, key: "", dayChangePercentage: 0, current: 0, profit: 0, profit_percentage: 0 };

  constructor(private router: Router, private quoteService: QuoteService, private favStockService: FavStocksService, private authService: AuthService, private route: ActivatedRoute, public dialog: MatDialog) {

  }
  displayedColumns = ['stock', 'quantity', 'price', 'lastPrice', 'dayChangePercentage', 'current', 'profit', 'profit_percentage'];
  exampleDatabase = new ExampleDatabase();
  dataSource: ExampleDataSource | null;

  @ViewChild(MatSort) sort: MatSort;
  @ViewChild('filter') filter: ElementRef;
  @ViewChild(MatPaginator) paginator: MatPaginator;

  CanEdit(val: boolean) {
    this.canEdit = val;
    if (val) {
      this.displayedColumns.push('key');
      this.displayedColumns.push('key1');
    }
    else {
      this.displayedColumns.pop();
      this.displayedColumns.pop();
    }
  }
  ngOnInit() {
    this.canEdit = false;
    this.dataSource = new ExampleDataSource(this.exampleDatabase, this.sort, this.paginator);

    Observable.fromEvent(this.filter.nativeElement, 'keyup')
      .debounceTime(150)
      .distinctUntilChanged()
      .subscribe(() => {
        if (!this.dataSource) { return; }
        this.dataSource.filter = this.filter.nativeElement.value;
      });

    this.authService.user$.subscribe(user => {
      if (user) {
        this.uid = user.uid;
        var portfolio$ = this.favStockService.getPortfolio(this.uid);
        portfolio$.subscribe(eArray => {
          if (eArray) {
            let v: Portfolio = { stock: "Total", price: 0, quantity: 0, lastPrice: 0, key: "", dayChangePercentage: 0, current: 0, profit: 0, profit_percentage: 0 };
            eArray.push(v);
            this.exampleDatabase.dataChange.next(eArray);
            this.refreshval(false);
          }
        });
      }
    });
    this.refreshval(true);
  }

  refreshval(refresh: boolean) {
    this.PrevClose = 0;
    this.profitPercentage = 0;
    if (this.exampleDatabase.data.length > 0)
      this.tot = this.exampleDatabase.data[this.exampleDatabase.data.length - 1];
    this.tot.price = 0;
    this.tot.quantity = 0;
    this.tot.lastPrice = 0;
    this.tot.dayChangePercentage = 0;
    this.tot.current = 0;
    this.tot.profit = 0;
    this.tot.profit_percentage = 0;
    let len = this.exampleDatabase.data.length - 1;
    //console.log("refresh called");
    if (refresh)
      setTimeout(() => this.refreshval(true), 30000);
    this.exampleDatabase.data.forEach(element => {
      if (element.stock != "Total")
        this.quoteService.getQuotes(element.stock).subscribe((p) => {
          element.lastPrice = p.latestPrice;
          element.dayChangePercentage = p.changePercent * 100;
          element.current = element.quantity * element.lastPrice;
          this.PrevClose += element.current * (1 - p.changePercent);
          element.profit = (element.lastPrice - element.price) * element.quantity;
          element.profit_percentage = element.lastPrice / element.price * 100 - 100;

          this.tot.price += element.price;
          this.tot.quantity += element.quantity;
          this.tot.lastPrice += element.lastPrice;


          this.tot.current += element.current;
          this.tot.profit += element.profit;
          this.tot.profit_percentage = this.tot.current / (this.tot.current - this.tot.profit) * 100 - 100;
          this.tot.dayChangePercentage = this.tot.current / this.PrevClose * 100 - 100;

          //this.tot.profit_percentage += element.profit_percentage / len;
          //console.log(p);
          //this.updateOrPush(p);
        });
    });
  }

  addorUpdateRecord(temp: Portfolio) {

    const copiedData = this.exampleDatabase.data.slice();
    copiedData.push(temp);
    this.exampleDatabase.dataChange.next(copiedData);
  }

  openDialog(v: Portfolio): void {
    let dialogRef = this.dialog.open(DialogChangeValue, {

      data: { data: v, alldata: this.exampleDatabase.data }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log(result);
      if (result)
        this.favStockService.updatePortfolio(this.uid, result);
    });
  }

  openAddDialog() {
    let v: Portfolio = { stock: "", price: 0, quantity: 0, lastPrice: 0, key: "", dayChangePercentage: 0, current: 0, profit: 0, profit_percentage: 0 };
    this.openDialog(v);
  }
}




/** An example database that the data source uses to retrieve data for the table. */
export class ExampleDatabase {
  /** Stream that emits whenever the data has been modified. */
  dataChange: BehaviorSubject<Portfolio[]> = new BehaviorSubject<Portfolio[]>([]);
  get data(): Portfolio[] { return this.dataChange.value; }

  constructor() {

  }

}

/**
 * Data source to provide what data should be rendered in the table. Note that the data source
 * can retrieve its data in any way. In this case, the data source is provided a reference
 * to a common data base, ExampleDatabase. It is not the data source's responsibility to manage
 * the underlying data. Instead, it only needs to take the data and send the table exactly what
 * should be rendered.
 */
export class ExampleDataSource extends DataSource<any> {

  _filterChange = new BehaviorSubject('');
  get filter(): string { return this._filterChange.value; }
  set filter(filter: string) { this._filterChange.next(filter); }
  constructor(private _exampleDatabase: ExampleDatabase, private _sort: MatSort, private _paginator: MatPaginator) {
    super();
  }

  /** Connect function called by the table to retrieve one stream containing the data to render. */
  connect(): Observable<Portfolio[]> {
    const displayDataChanges = [
      this._exampleDatabase.dataChange,
      this._sort.sortChange, this._filterChange,
      this._paginator.page,
    ];



    return Observable.merge(...displayDataChanges).map(() => {
      const startIndex = this._paginator.pageIndex * this._paginator.pageSize;
      return this.getSortedData().slice().filter((item: Portfolio) => {
        let searchStr = (item.stock).toLowerCase();
        return searchStr.indexOf(this.filter.toLowerCase()) != -1;
      }).splice(startIndex, this._paginator.pageSize);
    });
  }

  disconnect() { }

  /** Returns a sorted copy of the database data. */
  getSortedData(): Portfolio[] {
    const data = this._exampleDatabase.data.slice();
    if (!this._sort.active || this._sort.direction == '') { return data; }

    var tot: Portfolio = data.pop();


    var retData: Portfolio[] = data.sort((a, b) => {

      let propertyA: number | string = '';
      let propertyB: number | string = '';

      switch (this._sort.active) {
        case 'stock': [propertyA, propertyB] = [a.stock, b.stock]; break;
        case 'quantity': [propertyA, propertyB] = [a.quantity, b.quantity]; break;
        case 'price': [propertyA, propertyB] = [a.price, b.price]; break;
        case 'lastPrice': [propertyA, propertyB] = [a.lastPrice, b.lastPrice]; break;
        case 'dayChangePercentage': [propertyA, propertyB] = [a.dayChangePercentage, b.dayChangePercentage]; break;
        case 'current': [propertyA, propertyB] = [a.current, b.current]; break;
        case 'profit': [propertyA, propertyB] = [a.profit, b.profit]; break;
        case 'profit_percentage': [propertyA, propertyB] = [a.profit_percentage, b.profit_percentage]; break;
      }

      let valueA = isNaN(+propertyA) ? propertyA : +propertyA;
      let valueB = isNaN(+propertyB) ? propertyB : +propertyB;

      return (valueA < valueB ? -1 : 1) * (this._sort.direction == 'asc' ? 1 : -1);
    });
    retData.push(tot);
    return retData;
  }
}

////////////////
@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'dialog-component.html',
})
export class DialogChangeValue {
  data: Portfolio;
  alldata: Portfolio[];

  constructor(
    public dialogRef: MatDialogRef<DialogChangeValue>,
    @Inject(MAT_DIALOG_DATA) data: any) {
    this.data = data.data;
    this.alldata = data.alldata;
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  getErrorText(v: Portfolio) {
    let found = false;
    if (v.stock.length < 1) return "Select Symbol"

    this.alldata.forEach(element => {
      if (v.stock == element.stock && v.key.length < 2) {
        found = true;
      }
    });
    if (found) return "Symbol Already exist in Portfolio";
    if (v.price < 1) return "Enter Price"
    if (v.quantity < 1) return "Enter Quantity"
    // if (v.value == v.setValue) return '  ';
    // if (v.maxValue == v.minValue) return '';
    // if (v.setValue < v.minValue) return 'Min value is ' + v.minValue;
    // if (v.setValue > v.maxValue) return 'Max value is ' + v.maxValue;
    return '';
  }


}


