import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { News } from '../model/news';
import { NewsService } from '../service/news.service';

@Component({
  selector: 'app-news',
  templateUrl: './news.component.html',
  styleUrls: ['./news.component.css']
})
export class NewsComponent implements OnInit {
  selectedStock: string;
  news = new Array<News>();
  @Input() stsymbol: string = '';
  ngOnChanges(changes: SimpleChanges) {
    this.selectedStock = this.stsymbol;
    this.newsService.getNews(this.stsymbol).subscribe(val => { this.news = val; this.deleteUnwantedNews(); });
  }
  constructor(private newsService: NewsService) { }

  deleteUnwantedNews() {
    for (var i = 0; i < this.news.length; i++) {
      //console.log(this.news[i].summary.length);
      if (this.news[i].summary.length < 40) {
        this.news.splice(i, 1);
        //console.log(this.news[i].summary);
      }
    }
  }
  ngOnInit() {
  }

}



